.westHollywoodContent {
  padding: 60px 0;
  box-sizing: border-box;
  width: 100%;

  h1 {
    font-size: 48px;
    font-weight: 900;
    text-align: center;
    margin: 0 0 40px;
    color: #000;
  }

  h4 {
    font-size: 16px;
    font-weight: 300;
  }

  .info {
    margin-bottom: 60px;

    p {
      margin-bottom: 20px;
      font-size: 16px;
      line-height: 27px;
      font-weight: 300;
    }
  }

  ul {
    li {
      font-size: 16px;
      line-height: 27px;
      font-weight: 300;
    }
  }
}
